import { request } from './request'
//
function homeReq() {
  return request({
    url: '/menus'
  })
}
//--------------------------------------------------------------------------
//
function userListReq(params) {
  return request({
    url: '/users',
    params
  })
}
//
function saveState(id, state) {
  return request({
    url: `users/${id}/state/${state}`,
    method: 'put'
  })
}
// 
function addUser(data) {
  return request({
    url: '/users',
    method: 'post',
    data
  })
}
// 
function editUser(id) {
  return request({
    url: `/users/${id}`
  })
}
// 
function saveEditUser(id, email, mobile) {
  return request({
    url: `/users/${id}`,
    method: 'put',
    data: {
      email,
      mobile
    }
  })
}
// 
function deleteUser(id) {
  return request({
    url: `/users/${id}`,
    method: 'delete'
  })
}
// 
function setRole(id, rid) {
  return request({
    url: `/users/${id}/role`,
    method: 'put',
    data: {
      rid
    }
  })
}
//-----------------------------------------------------------------------------------------
// 
function getRightsList(type) {
  return request({
    url: `/rights/${type}`
  })
}
// 
function getRolesList() {
  return request({
    url: '/roles'
  })
}
// 
function deleteRights(roleId, rightId) {
  return request({
    url: `/roles/${roleId}/rights/${rightId}`,
    method: 'delete'
  })
}
//
function allotRights(roleId, rids) {
  return request({
    url: `/roles/${roleId}/rights`,
    method: 'post',
    data: {
      rids
    }
  })
}
//
function addRole(data) {
  return request({
    url: '/roles',
    method: 'post',
    data
  })
}
//id
function findRole(roleId) {
  return request({
    url: `/roles/${roleId}`
  })
}
//
function editRole(roleId, roleName, roleDesc) {
  return request({
    url: `/roles/${roleId}`,
    method: 'put',
    data: {
      roleName,
      roleDesc
    }
  })
}
//
function deleteRole(roleId) {
  return request({
    url: `/roles/${roleId}`,
    method: 'delete'
  })
}
//-------------------------------------------------------------------------------
function getCategories(params) {
  return request({
    url: '/categories',
    params
  })
}
//
function addCate(data) {
  return request({
    url: '/categories',
    method: 'post',
    data
  })
}
//id
function getCateById(cateId) {
  return request({
    url: `/categories/${cateId}`
  })
}

//
function editCate(cateId, cat_name) {
  return request({
    url: `/categories/${cateId}`,
    method: 'put',
    data: {
      cat_name
    }
  })
}
//
function deleteCate(cateId) {
  return request({
    url: `/categories/${cateId}`,
    method: 'delete'
  })
}
// -------------------------------------------------------
// 
function getCateParams(cateId, sel) {
  return request({
    url: `/categories/${cateId}/attributes`,
    params: {
      sel
    }
  })
}
//
function addParams(cateId, attr_name, attr_sel) {
  return request({
    url: `/categories/${cateId}/attributes`,
    method: 'post',
    data: {
      attr_name,
      attr_sel
    }
  })
}

// ID 
function getCateParamById(cateId, attr_id, attr_sel) {
  return request({
    url: `/categories/${cateId}/attributes/${attr_id}`,
    params: {
      attr_sel
    }
  })
}
//
function editParam(cateId, attr_id, attr_name, attr_sel, attr_vals) {
  return request({
    url: `/categories/${cateId}/attributes/${attr_id}`,
    method: 'put',
    data: {
      attr_name,
      attr_sel,
      attr_vals
    }
  })
}
//  
function deleteParam(cateId, attr_id) {
  return request({
    url: `/categories/${cateId}/attributes/${attr_id}`,
    method: 'delete'
  })
}
//--------------------------------------------------------------
//
function getGoodsList(params) {
  return request({
    url: '/goods',
    params
  })
}
//
function deleteGoods(goodsId) {
  return request({
    url: `/goods/${goodsId}`,
    method: 'delete'
  })
}
//
function addGoods(data) {
  return request({
    url: '/goods',
    method: 'post',
    data
  })
}
//
function findGoods(goodsId) {
  return request({
    url: `/goods/${goodsId}`
  })
}
//
function editGoods(goodsId, data) {
  return request({
    url: `/goods/${goodsId}`,
    method: 'put',
    data
  })
}
// -----------------------------------------------------
// 
function getOrderList(params) {
  return request({
    url: '/orders',
    params
  })
}

//
function getLogistics() {
  return request({
    url: '/kuaidi/1106975712662'
  })
}


//
function getReports() {
  return request({
    url: '/reports/type/1'
  })
}

export {
  homeReq,
  userListReq,
  saveState,
  addUser,
  editUser,
  saveEditUser,
  deleteUser,
  getRightsList,
  getRolesList,
  deleteRights,
  allotRights,
  setRole,
  editRole,
  deleteRole,
  addRole,
  findRole,
  getCategories,
  addCate,
  editCate,
  deleteCate,
  getCateParams,
  getCateById,
  addParams,
  getCateParamById,
  editParam,
  deleteParam,
  getGoodsList,
  deleteGoods,
  addGoods,
  findGoods,
  editGoods,
  getOrderList,
  getLogistics,
  getReports
}